<template>
  <el-dialog
    title="上传文件"
    :visible.sync="dialogVisible"
    width="50%"
  >
    <el-upload
      class="upload-demo"
      :limit="1"
      :action="uploadUrl"
      :auto-upload="false"
      :headers="headerData"
      :file-list="imgList"
      name="targetFile"
      ref="upload"

    >
      <el-button slot="trigger" size="small" type="primary">选择文件</el-button>
      <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
    </el-upload>
  </el-dialog>
</template>

<script>
  import {getUploadAddress} from '../../utils/api'
  export default{
    data(){
      return {
        dialogVisible:false,
        uploadUrl:'',
        headerData:{},
        imgList:[]
      }
    },
    mounted(){

      const token = localStorage.getItem('token')
      this.headerData.token = token
    },
    methods: {
      submitUpload(){
        this.$refs.upload.submit();
      },
      show(type){
        this.uploadUrl = getUploadAddress(this.$route.params.id, type)
        this.dialogVisible = true
      }
    }
  }
</script>