import {get, post,del, patch} from './axios'

const indexWord = '/niu'
export const login = (data) => {
  const url = indexWord + '/login';
  return post(url, data)
}

const contentBasePath = indexWord + '/api/v1/content';

export const addContent = (data) => {
  return post(contentBasePath,data)
}

export const getContent = () => {
  return get(contentBasePath)
}

export const delContent = (id) => {
  return del(`${contentBasePath}?id=${id}`)
}

export const patchContent = (data)  => {
  return patch(contentBasePath, data)
}

const  contentUrlc = contentBasePath + '/getContentById'
export const getContentById = (id) => {
  return get(contentUrlc + `?id=${id}`)
}

export const getFilesByContentId = (content_id) => {
  return get(contentBasePath + '/getFilesByContentId?content_id=' + content_id)
}

export const deleteFileByFileId = (fileName) => {
  return del(contentBasePath + '/deleteFileByFileName?fileName='+fileName)
}

export const getUploadAddress = (content_id, file_type) => {
  return contentBasePath + `/${content_id}/upload_file/${file_type}`
}