<template>
  <el-dialog
    :title="title"
    :visible.sync="dialogVisible"
    width="50%"
  >
    <el-radio-group v-model="type">
      <el-radio :label=1>文本</el-radio>
      <el-radio :label=2>图片地址</el-radio>
      <el-radio :label=3>音频地址</el-radio>
    </el-radio-group>
    <div v-if="type !== 1">
      <p v-for="f in files" :key="f.id" v-if="String(f.file_type) === String(type)">
        <img :src="'/niu/img/'+f.file_name" style="width: 60px;height: 60px">
        {{f.file_name}}
        <el-button @click="addToContent(f)" type="success" icon="el-icon-circle-plus-outline" circle></el-button>
        <el-button @click="deleteFile(f)" type="danger" icon="el-icon-delete" circle></el-button>
      </p>
    </div>
    <el-form labelWidth="59px" labelPosition="left">
      <el-form-item label="内容">
        <el-input
          type="textarea"
          :rows="2"
          placeholder="请输入内容"
          v-model="data">
        </el-input>
      </el-form-item>
      <el-form-item label="备注">
        <el-input
          type="textarea"
          :rows="2"
          placeholder="请输入内容"
          v-model="desc">
        </el-input>
      </el-form-item>
    </el-form>

    <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="addTarea">确 定</el-button>
  </span>
  </el-dialog>
</template>

<script>
  import { getFilesByContentId, deleteFileByFileId } from '../../utils/api'
  export default {
    data (){
      return {
        type : 2,
        dialogVisible : false,
        title: '',
        data: '',
        desc: '',
        index:'',
        files: []
      }
    },
    mounted(){
      this.getFiles()
    },
    methods: {
      getFiles(){
        getFilesByContentId(this.$route.params.id).then(res => {
          this.files = res.data.data;
        })
      },
      addToContent(file){
        this.data = '/img/'+file.file_name
      },
      deleteFile(file){
        deleteFileByFileId(file.file_name).then(res => {
          this.getFiles();
        })
      },
      showAdd(){
        this.dialogVisible=true;
        this.isAddShow = true
        this.type=2;
        this.data = '';
        this.desc = ''
      },
      showEdit(d,index){
        this.index = index
        this.dialogVisible= true
        this.isAddShow = false
        this.data = d.data;
        this.desc = d.desc;
        this.type = d.type
      },
      addTarea () {
        if(this.isAddShow){
          this.$emit('addDialog',this.type, this.data,this.desc)
          this.data = ''
          this.desc = ''
          this.dialogVisible = false
        }else {
          debugger
          this.$emit('addTextare',this.index,this.type, this.data,this.desc)
          this.data = ''
          this.desc = ''
          this.dialogVisible = false
        }

      }
    }
  }
</script>
